import * as actionTypes from '../Constant/actionTypes';

export function submitUser(data) {
    return { payload: data, type: actionTypes.ADD_USER }
}

export function removeUser(id) {
    return { payload: id, type: actionTypes.REMOVE_USER }
}

export function updateUser(id, data) {
    return { id: id, data: data, type: actionTypes.UPDATE_USER }
}

export function startUpdateUser(id) {
    return { type: actionTypes.START_UPDATE, id: id }
}
