import React, { Component, Fragment } from 'react';
import { Space, Popconfirm } from 'antd';
import { DeleteTwoTone, EditTwoTone } from '@ant-design/icons'
import Col from 'react-bootstrap/Col';
import UserForm from '../Components/Form/UserForm';
import List from '../Components/List/List';
import Row from 'react-bootstrap/Row';
import classes from './Dashboard.module.css';
import { connect } from 'react-redux';
import * as actions from '../Redux/Action/action';

class Dashboard extends Component {
    render() {
        const columns = [
            {
                title: 'Id',
                dataIndex: 'id',
                key: 'id'
            }, {
                title: 'Name',
                dataIndex: 'Name',
                key: 'name'
            }, {
                title: 'Age',
                dataIndex: 'Age',
                responsive: ['md'],
                key: 'age'
            }, {
                title: 'Country',
                dataIndex: 'Country',
                responsive: ['md'],
                key: 'country'
            }, {
                title: 'State',
                dataIndex: 'State',
                key: 'state',
                responsive: ['lg']
            }, {
                title: 'City',
                dataIndex: 'City',
                responsive: ['lg'],
                key: 'city'
            }, {
                title: 'Action',
                key: 'action',
                render: (text, record) => (
                    <Space size="large">
                        <EditTwoTone key='update' onClick={() => this.props.onUpdateStart(record.id)} />
                        <Popconfirm title="Sure to delete?" onConfirm={() => this.props.onConfirmDelete(record.id)}>
                            <DeleteTwoTone />
                        </Popconfirm>
                    </Space>
                ),
            },
        ];

        return (
            <Fragment>
                <header className={classes.Header}>Dashboard</header>
                <Row className={classes.Row}>
                    <Col xs={12} sm={12} md={6} lg={6}>
                        {
                            this.props.update === true &&
                            this.props.users.filter((data) => data.id === this.props.updateID).map((data) =>
                                <UserForm
                                    header='UPDATE USER'
                                    key={data.id}
                                    title={'Update'}
                                    data={data}
                                    onCreate={this.props.update ? this.props.onUpdateUser : this.props.onSubmitUsers}
                                />
                            )
                        }
                        {
                            this.props.update === false &&
                            <UserForm
                                header='ADD NEW USER'
                                title={'Add'}
                                onCreate={this.props.update ? this.props.onUpdateUser : this.props.onSubmitUsers}
                            />
                        }
                    </Col>
                    <Col xs={12} sm={12} md={6} lg={6}>
                        <List
                            header='LIST OF ALL USERS'
                            columns={columns}
                            data={this.props.users}
                        />
                    </Col>
                </Row>
            </Fragment>
        );
    }
}

const mapStatetoProp = state => {
    return {
        users: state.users,
        id: state.userID,
        update: state.update,
        updateID: state.updateID,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onSubmitUsers: (data) => dispatch(actions.submitUser(data)),
        onConfirmDelete: (id) => dispatch(actions.removeUser(id)),
        onUpdateUser: (id, data) => dispatch(actions.updateUser(id, data)),
        onUpdateStart: (id) => dispatch(actions.startUpdateUser(id)),
    }
}

export default connect(mapStatetoProp, mapDispatchToProps)(Dashboard);
