import * as Yup from 'yup';
import { ErrorMessage, Field, Form, Formik } from 'formik';
import React from 'react';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import classes from './UserForm.module.css';

const RegistrationForm = (props) => {
    const { data } = props;
    const updatedValues = (values) => {
        props.onCreate(
            data.id,
            values
        )
    }
    const submitValues = (values) => {
        props.onCreate(values)
    }

    return (
        <Formik
            initialValues={
                props.title === 'Update'
                    ? {
                        Age: data.Age,
                        City: data.City,
                        Country: data.Country,
                        Name: data.Name,
                        State: data.State
                    }
                    : {
                        Age: '',
                        City: '',
                        Country: '',
                        Name: '',
                        State: ''
                    }
            }
            validationSchema={
                Yup.object().shape({
                    Age: Yup.string().matches(/^(1[89]|[2-9]\d)$/, 'Your Age Should be In Between 18 years to 99 years.').required('Your Age is required*'),
                    City: Yup.string().max(20, 'Must be 20 characters or less').required('Your City name is required*'),
                    Country: Yup.string().required('Your Country name is required*').max(20, 'Must be 20 characters or less'),
                    Name: Yup.string().max(15, 'Must be 15 characters or less').matches(/^[a-zA-Z]+(([',. -][a-zA-Z ])?[a-zA-Z]*)*$/, 'Use Alphabates only').required('Your Name is required*'),
                    State: Yup.string().required('Your State name is required*').max(20, 'Must be 20 characters or less')
                })
            }
            onSubmit={(values, { resetForm }) => {
                if (props.title === 'Add') {
                    submitValues(values);
                    resetForm();
                }
                if (props.title === 'Update') {
                    updatedValues(values);
                    resetForm();
                }
            }}
        >
            {({ errors, touched }) => (
                <Form>
                    <div className="form-group">
                        <header className={classes.Header}>{props.header}</header>
                        <div className='mt-5'>
                            <Row className="justify-content-md-center">
                                <Col lg={6} md={6} sm={12} xs={12}>
                                    <div className="form-group col">
                                        <label htmlFor="Name" className='font-weight-bolder'>Name</label>
                                        <Field type="text" name="Name" placeholder="Name" className={[`form-control ${errors.Name && touched.Name ? 'is-invalid' : ''}`, classes.Input].join(' ')}></Field>
                                        <ErrorMessage name="Name" component="div" className={[classes.ErrorMessage, 'invalid-feedback'].join(' ')} />
                                    </div>
                                </Col>
                            </Row>
                            <Row className="justify-content-md-center">
                                <Col lg={6} md={6} sm={12} xs={12}>
                                    <div className="form-group col">
                                        <label htmlFor="Age" className='font-weight-bolder'>Age</label>
                                        <Field type="number" name="Age" placeholder="Age" className={[`form-control ${errors.Age && touched.Age ? 'is-invalid' : ''}`, classes.Input].join(' ')}></Field>
                                        <ErrorMessage name="Age" component="div" className={[classes.ErrorMessage, 'invalid-feedback'].join(' ')} />
                                    </div>
                                </Col>
                            </Row>
                            <Row className="justify-content-md-center">
                                <Col lg={6} md={6} sm={12} xs={12}>
                                    <div className="form-group col">
                                        <label htmlFor="City" className='font-weight-bolder'>City</label>
                                        <Field type="text" name="City" placeholder="City" className={[`form-control ${errors.City && touched.City ? 'is-invalid' : ''}`, classes.Input].join(' ')}></Field>
                                        <ErrorMessage name="City" component="div" className={[classes.ErrorMessage, 'invalid-feedback'].join(' ')} />
                                    </div>
                                </Col>
                            </Row>
                            <Row className="justify-content-md-center">
                                <Col lg={6} md={6} sm={12} xs={12}>
                                    <div className="form-group col">
                                        <label htmlFor="State" className='font-weight-bolder'>State</label>
                                        <Field type="text" name="State" placeholder="State" className={[`form-control ${errors.State && touched.State ? 'is-invalid' : ''}`, classes.Input].join(' ')}></Field>
                                        <ErrorMessage name="State" component="div" className={[classes.ErrorMessage, 'invalid-feedback'].join(' ')} />
                                    </div>
                                </Col>
                            </Row>
                            <Row className="justify-content-md-center">
                                <Col lg={6} md={6} sm={12} xs={12}>
                                    <div className="form-group col">
                                        <label htmlFor="Country" className='font-weight-bolder'>Country</label>
                                        <Field type="text" name="Country" placeholder="Country" className={[`form-control ${errors.Country && touched.Country ? 'is-invalid' : ''}`, classes.Input].join(' ')}></Field>
                                        <ErrorMessage name="Country" component="div" className={[classes.ErrorMessage, 'invalid-feedback'].join(' ')} />
                                    </div>
                                </Col>
                            </Row>
                        </div>
                        <div className="form-group text-center mt-3">
                            <div className="d-flex justify-content-center">
                                {
                                    props.title === 'Update'
                                        ? <div className="d-flex">
                                            <div className="mr-4">
                                                <button type="submit" className={[classes.Button, classes.Update].join(' ')}>UPDATE</button>
                                            </div>
                                            <div>
                                                <button type="submit" onClick={props.clicked} className={[classes.Button, classes.Cancel].join(' ')}>CANCEL</button>
                                            </div>
                                        </div>
                                        : <div className="d-flex">
                                            <div className="mr-4">
                                                <button type="submit" className={[classes.Button, classes.Submit].join(' ')}>SUBMIT</button>
                                            </div>
                                            <div>
                                                <button type="reset" className={[classes.Button, classes.Reset].join(' ')}>RESET</button>
                                            </div>
                                        </div>
                                }
                            </div>
                        </div>
                    </div>
                </Form>
            )
            }
        </Formik >
    );
};

export default RegistrationForm;
