import * as actionTypes from '../Constant/actionTypes';
import { updateObject } from '../Utility/Utility';

const initialState = {
    users: [],
    userID: Date.now().toString(36) + Math.random().toString(36).substr(2, 5).toUpperCase(),
    update: false,
    updateID: null
}

const addUser = (state, action) => {
    const newData = {
        id: state.userID,
        key: state.userID,
        ...action.payload
    };
    const manipulatedState = [...state.users, newData]
    return updateObject(state, {
        users: manipulatedState,
        userID: Date.now().toString(36) + Math.random().toString(36).substr(2, 5).toUpperCase()
    });
}

const removeUser = (state, action) => {
    const dataSource = [...state.users];
    return updateObject(state, {
        users: dataSource.filter(item => item.key !== action.payload),
    });
}

const updateUser = (state, action) => {
    const current = [...state.users];
    const objIndex = state.users.findIndex((obj) => obj.id === action.id);
    const updatedObj = { id: action.id, key: action.id, ...action.data };
    const newUsers = Object.assign(
        [],
        current,
        { [objIndex]: updatedObj }
    );
    return updateObject(state, {
        users: newUsers,
        update: false,
        updateID: null
    });
}

const startUpdating = (state, action) => {
    return updateObject(state, {
        update: true,
        updateID: action.id
    });
}

const rootReducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.ADD_USER: return addUser(state, action);
        case actionTypes.REMOVE_USER: return removeUser(state, action);
        case actionTypes.UPDATE_USER: return updateUser(state, action);
        case actionTypes.START_UPDATE: return startUpdating(state, action);
        default: return state;
    }
};

export default rootReducer;
